import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  Host,
  Optional,
} from '@angular/core';
import { BcHelperService } from '@bancolombia/design-system-web/bc-helpers';
import { BcRadioGroupComponent } from './bc-radio-group/bc-radio-group.component';
/**
 * Componente Radio Button
 *
 * @export
 * @class BcRadioComponent
 * @implements {OnInit}
 */
@Component({
  selector: 'bc-radio',
  templateUrl: 'bc-radio.component.html',
})
export class BcRadioComponent implements OnInit {
  /**
   * Id del elemento.
   *
   * @type {string}
   * @memberof BcRadioComponent
   */
  @Input() id = '';
  /**
   * Nombre del elemento radio.
   *
   * @type {string}
   * @memberof BcRadioComponent
   */
  @Input() name = '';
  /**
   * Valor del elemento.
   *
   * @type {string}
   * @memberof BcRadioComponent
   */
  @Input() value = '';
  /**
   * Indica si el elemento esta habilitado.
   *
   * @type {boolean}
   * @memberof BcRadioComponent
   */
  @Input() enabled = true;
  /**
   * Indica si el elemento esta seleccionado.
   *
   * @type {boolean}
   * @memberof BcRadioComponent
   */
  isChecked = false;

  /**
   * Valor seleccionado.
   *
   * @type {EventEmitter<any>}
   * @memberof BcRadioComponent
   */
  @Output() changeStateForParent: EventEmitter<any> = new EventEmitter<any>();

  outline = false;

  constructor(
    @Optional() @Host() private radioGroup: BcRadioGroupComponent,
    private helper: BcHelperService
  ) {}

  ngOnInit(): void {
    if (this.radioGroup) {
      this.radioGroup.onDisabled.subscribe((isDisabledRadioGroup) => {
        this.toogleDisabled(isDisabledRadioGroup);
      });
    }
  }

  /**
   * Cambia el estado al seleccionar elemento.
   *
   * @param {*} event
   * @memberof BcRadioComponent
   */
  toggle(event: any) {
    this.isChecked = !this.isChecked;
    this.changeStateForParent.emit(event);
    if (this.radioGroup) {
      this.radioGroup.onTouch();
      this.radioGroup.set(this.value);
      this.radioGroup.changeValue.emit(this.value);
    }
  }

  /**
   * Habilita o deshabilita el label e input del radio.
   *
   * @param {boolean} isDisabled
   * @memberof BcRadioComponent
   */
  toogleDisabled(isDisabled: boolean) {
    this.enabled = !isDisabled;
  }

  /**
   * Accesibilidad.
   *
   * @param {KeyboardEvent} event
   * @memberof BcRadioComponent
   */
  keyEvent(event: KeyboardEvent) {
    this.outline = this.helper.keyEvent(event, {
      event: () => {
        this.changeStateForParent.emit(event);
      },
    });
  }

  get isRadioChecked() {
    if (this.radioGroup) {
      this.isChecked = this.radioGroup.contains(this.value);
    }
    return this.isChecked;
  }

  get radioGroupName() {
    if (this.radioGroup && this.radioGroup.name) {
      this.name = this.radioGroup.name;
    }
    return this.name;
  }
}
