import {
  Component,
  Output,
  EventEmitter,
  Input,
  Optional,
  Self,
  OnInit,
} from '@angular/core';
import { BcGeneralInputComponent } from '@bancolombia/design-system-web/bc-helpers';
import { NgControl } from '@angular/forms';

@Component({
  selector: 'bc-radio-group',
  templateUrl: './bc-radio-group.component.html',
})
export class BcRadioGroupComponent
  extends BcGeneralInputComponent
  implements OnInit
{
  @Output() changeValue = new EventEmitter<any>();
  @Output() onDisabled = new EventEmitter<boolean>();
  @Input() disabled = false;
  @Input() defaultValue = undefined;
  @Input() name: string;
  @Input() background: 'regular' | 'dark' = 'regular';

  constructor(@Self() @Optional() public controlDirective: NgControl) {
    super();
    if (controlDirective) {
      controlDirective.valueAccessor = this as BcGeneralInputComponent;
    }
  }

  ngOnInit(): void {
    this.writeValue(this.defaultValue);
  }

  setDisabledState(isDisabled: boolean) {
    this.disabled = isDisabled;
    this.onDisabled.emit(isDisabled);
  }

  getIsDisabled() {
    return this.disabled;
  }

  writeValue(value) {
    if (typeof value != 'undefined') {
      this.changeValue.emit(value);
      this.value = value;
    }
  }
}
